.switch {
    position: relative;
    display: inline-block;
    margin: 0 5px;
    cursor: pointer;
}

.switch > span {
    position: absolute;
    top: 14px;
    pointer-events: none;
    font-family: "Helvetica", Arial, sans-serif;
    font-weight: bold;
    font-size: 12px;
    text-transform: uppercase;
    text-shadow: 0 1px 0 rgba(0, 0, 0, 0.06);
    width: 50%;
    text-align: center;
}

input.check-toggle-round-flat:checked ~ .off {
    color: #f32552;
}

input.check-toggle-round-flat:checked ~ .on {
    color: #fff;
}

.switch > span.on {
    left: 1px;
    padding-left: -5px;
    color: #f32552;
    top: 10px;
    font-size: 1rem;
}

.switch > span.off {
    right: 4px;
    padding-right: 2px;
    color: #fff;
    top: 11px;
    font-size: 1rem;
}

.check-toggle {
    position: absolute;
    margin-left: -9999px;
    visibility: hidden;
}
.check-toggle + label {
    display: block;
    position: relative;
    cursor: pointer;
    outline: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

input.check-toggle-round-flat + label {
    padding: 2px;
    width: 97px;
    height: 35px;
    background-color: #ffffff;
    border-radius: 60px;
}
input.check-toggle-round-flat + label:before,
input.check-toggle-round-flat + label:after {
    display: block;
    position: absolute;
    content: "";
}

input.check-toggle-round-flat + label:before {
    top: 2px;
    left: 2px;
    bottom: 2px;
    right: 2px;
    background-color: #000000;
    border-radius: 60px;
}
input.check-toggle-round-flat + label:after {
    top: 4px;
    left: 4px;
    bottom: 4px;
    width: 48px;
    background-color: #fff;
    border-radius: 52px;
    transition: margin 0.2s;
}

input.check-toggle-round-flat:checked + label:after {
    margin-left: 41px;
}

.tm-container {
    height: 80%;
}

.light-mode {
    background-color: #fff;
    color: #333;
    transition: background-color 0.25s ease-out;
}

.light-mode nav {
    transition: background-color 0.25s ease-out;
}

.dark-mode {
    background-color: #1f1f1f;
    color: #f4f1eb;
}

.dark-mode nav {
    background-color: #1b2938;
}

.tm-main {
    text-align: center;
    font-family: montserrat;
    padding: 0.5em;
    margin: 0 auto;
    max-width: 600px;
    font-size: 1.2rem;
    line-height: 1.1;
    width: 85%;
    height: 100%;
    padding: 100px 0;
}

nav {
    width: 100%;
    display: flex;
    background-color: steelblue;
    padding: 20px;
}

h1 {
    margin: 0;
}

.toggle {
    position: relative;
    padding: 0 4px;
    display: flex;
    align-items: center;
}

input[type="checkbox"].checkbox {
    width: 40px;
    height: 10px;
    background: #555;
    position: relative;
    border-radius: 5px;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    cursor: pointer;
    vertical-align: 2px;
    outline: none;
}

input[type="checkbox"].checkbox:checked + label {
    left: 30px;
}

input[type="checkbox"].checkbox:focus-visible {
    outline: solid 2px white;
}

input[type="checkbox"].checkbox + label {
    display: inline-block;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    transition: all 0.3s ease;
    cursor: pointer;
    position: absolute;
    left: 2px;
    background: #fff;
    opacity: 0.9;
    background-color: cyan;
}

.toggle-container {
    display: flex;
    margin: 0 auto;
}

.toggle-container > span {
    font-size: 1.2em;
    background: none;
    border: none;
    cursor: pointer;
    transition: color 0.3s ease;
    margin-top: 4px;
    margin-right: 2px;
}

.toggle-container > span:last-child {
    margin-top: 5px;
}

.pc-container {
    perspective: 1000px;
}

.pc-card {
    min-height: 70%;
    width: 40vh;
    box-shadow: 0 20px 20px rgba(0, 0, 0, 0.2), 0 0 50px rgba(0, 0, 0, 0.2);
    border-radius: 30px;
    padding: 3rem 1rem;
    transform-style: preserve-3d;
    background: #353535ef;
    /* cursor: pointer; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.pc-pic {
    min-height: 35vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.img {
    width: 12rem;
    z-index: 2 !important;
    transition: all 0.75s ease-out;
}

.pc-bg-circle {
    width: 12rem;
    height: 12rem;
    border-radius: 50%;
    background: linear-gradient(
        rgba(245, 70, 66, 0.75),
        rgba(8, 83, 156, 0.75)
    );
    position: absolute;
    z-index: 1;
    transition: all 0.5s ease-out;
    box-shadow: 0 20px 20px rgba(0, 0, 0, 0.5), 0 0 50px rgba(0, 0, 0, 0.2);
}

.pc-info {
    text-align: center;
}



.pc-desc {
    font-size: 1.3rem;
    padding-top: 2rem 0;
    color: #ccb6b6;
    font-weight: lighter;
    transition: all 0.75s ease-out;
}

.pc-title {
    font-size: 2.5rem;
    transition: all 0.75s ease-out;
    margin: 10px 0;
    color: rgb(255, 255, 255);
    background: linear-gradient(
        to right,
        rgb(255, 0, 128),
        rgb(0, 132, 255),
        rgb(0, 225, 255),
        /* rgb(0, 255, 170), */
        rgb(0, 255, 128)
    );
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    -webkit-filter: drop-shadow(0px 5px 7px rgb(10, 10, 10));
            filter: drop-shadow(0px 5px 7px rgb(10, 10, 10));
    font-family: var(--dosis-font);
    text-transform: uppercase;
}




