.pc-container {
    perspective: 1000px;
}

.pc-card {
    min-height: 70%;
    width: 40vh;
    box-shadow: 0 20px 20px rgba(0, 0, 0, 0.2), 0 0 50px rgba(0, 0, 0, 0.2);
    border-radius: 30px;
    padding: 3rem 1rem;
    transform-style: preserve-3d;
    background: #353535ef;
    /* cursor: pointer; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.pc-pic {
    min-height: 35vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.img {
    width: 12rem;
    z-index: 2 !important;
    transition: all 0.75s ease-out;
}

.pc-bg-circle {
    width: 12rem;
    height: 12rem;
    border-radius: 50%;
    background: linear-gradient(
        rgba(245, 70, 66, 0.75),
        rgba(8, 83, 156, 0.75)
    );
    position: absolute;
    z-index: 1;
    transition: all 0.5s ease-out;
    box-shadow: 0 20px 20px rgba(0, 0, 0, 0.5), 0 0 50px rgba(0, 0, 0, 0.2);
}

.pc-info {
    text-align: center;
}



.pc-desc {
    font-size: 1.3rem;
    padding-top: 2rem 0;
    color: #ccb6b6;
    font-weight: lighter;
    transition: all 0.75s ease-out;
}

.pc-title {
    font-size: 2.5rem;
    transition: all 0.75s ease-out;
    margin: 10px 0;
    color: rgb(255, 255, 255);
    background: linear-gradient(
        to right,
        rgb(255, 0, 128),
        rgb(0, 132, 255),
        rgb(0, 225, 255),
        /* rgb(0, 255, 170), */
        rgb(0, 255, 128)
    );
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    filter: drop-shadow(0px 5px 7px rgb(10, 10, 10));
    font-family: var(--dosis-font);
    text-transform: uppercase;
}



